@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600;700&display=swap);


.score{justify-self:end;background:#ffffff;text-align:center;padding:0.8rem 2rem;border-radius:0.5rem}.score .caption{text-transform:uppercase;letter-spacing:3px;color:#2a46c0;font-size:0.75rem}.score .number{color:#3b4363;font-size:3rem;line-height:1}

header{display:grid;grid-template-columns:1fr 1fr;align-items:center;border:3px solid rgba(255,255,255,0.35);border-radius:0.8rem;padding:0.8rem 1rem}header h1{font-size:1.65em;margin:0;line-height:0.9;text-transform:uppercase}header h1 span{display:block}

.btn-choice{outline:none;border:none;cursor:pointer;display:inline-block;border-radius:50%;width:140px;height:140px;position:relative;background:#bcc1d4;background:linear-gradient(180deg, #bcc1d4 50%, #fff 50%)}.btn-choice .shadow-circle{display:inline-block;width:140px;height:140px;border-radius:50%;position:absolute;top:50%;left:50%;z-index:-1;transform:translate(-50%, -45%)}.btn-choice .icon{position:absolute;top:50%;left:50%;transform:translate(-50%, -47%);z-index:3;background-color:#E5E4E1;border-radius:50%;width:104px;height:97px}.btn-choice .icon::before{display:inline-block;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.btn-choice.scissors{border:18px solid #eca922}.btn-choice.scissors .shadow-circle{background-color:#C5701A}.btn-choice.scissors .icon::before{content:url(/static/media/icon-scissors.bec2f9b3.svg)}.btn-choice.paper{border:18px solid #5671f5}.btn-choice.paper .shadow-circle{background-color:#2947C0}.btn-choice.paper .icon::before{content:url(/static/media/icon-paper.69a8c2e9.svg)}.btn-choice.rock{border:18px solid #dd405d}.btn-choice.rock .shadow-circle{background-color:#9D1634}.btn-choice.rock .icon::before{content:url(/static/media/icon-rock.8ce1e109.svg)}.btn-choice.lizard{border:18px solid #8c5de5}.btn-choice.lizard .shadow-circle{background-color:#6037AD}.btn-choice.lizard .icon::before{content:url(/static/media/icon-lizard.7d2e0b18.svg)}.btn-choice.spock{border:18px solid #52bed1}.btn-choice.spock .shadow-circle{background-color:#2C8EAA}.btn-choice.spock .icon::before{content:url(/static/media/icon-spock.b6446e8e.svg)}

.set{position:relative;height:100%}.set:before{content:url(/static/media/bg-pentagon.aced7052.svg);position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.set .container-btn{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.set .container-btn:first-child{transform:translate(-53%, -160%)}.set .container-btn:nth-child(2){transform:translate(70%, -79%)}.set .container-btn:nth-child(3){transform:translate(24%, 49%)}.set .container-btn:nth-child(4){transform:translate(-124%, 49%)}.set .container-btn:nth-child(5){transform:translate(-170%, -79%)}@media (max-width: 480px){.set{transform:scale(0.8);margin:0}}@media (max-width: 375px){.set{transform:scale(0.7) translateY(-15%)}}@media (max-width: 320px){.set{transform:scale(0.6) translateY(-15%)}}

.set-two{padding-top:4rem;display:grid;grid-template-columns:1fr 1fr;justify-items:center;align-content:start;text-align:center}.set-two.show-result{grid-template-columns:1fr 1fr 1fr;width:70%;margin:0 auto}.set-two h2{letter-spacing:2px;text-transform:uppercase;font-size:1.25em;margin-bottom:3rem}.set-two .spot{position:relative;width:220px;height:220px;display:inline-block}.set-two .spot::before{content:'';display:inline-block;position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);width:180px;height:180px;background-color:#141539;border-radius:50%}.set-two .btn-choice{width:220px;height:220px;border-width:25px;cursor:default}.set-two .btn-choice .shadow-circle{width:220px;height:220px}.set-two .btn-choice .icon{width:172px;height:164px}.set-two .btn-choice .icon::before{transform:translate(-50%, -50%) scale(1.5)}.set-two .result{text-align:center;align-self:center}.set-two .result .text{font-size:3em;text-transform:uppercase;margin-bottom:1rem}.set-two .result .btn-play{outline:none;border:none;cursor:pointer;padding:1rem 3rem;background-color:#ffffff;border-radius:0.5rem;letter-spacing:1px;text-transform:uppercase;font-size:0.8em}.set-two .result .btn-play.win{color:#3b4363}.set-two .result .btn-play.lose{color:#dd405d}.set-two .win{position:relative;width:220px;height:220px}.set-two .win .circle-win{position:absolute;top:50%;left:50%;transform:translate(-50%, -48%);width:220px;height:220px;z-index:-4}.set-two .win .circle-win span{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);border-radius:50%;background-color:#2B3956;box-shadow:0px 0px 8px 6px rgba(20,21,57,0.22);opacity:0}.set-two .win .circle-win span:first-child{width:300px;height:300px;z-index:-2;-webkit-animation:fade 1s ease 0.25s infinite forwards;animation:fade 1s ease 0.25s infinite forwards}.set-two .win .circle-win span:nth-child(2){width:400px;height:400px;z-index:-3;-webkit-animation:fade 1s ease 0.5s infinite  forwards;animation:fade 1s ease 0.5s infinite  forwards}.set-two .win .circle-win span:last-child{width:550px;height:550px;z-index:-4;-webkit-animation:fade 1s ease 1s infinite forwards;animation:fade 1s ease 1s infinite forwards}@-webkit-keyframes fade{0%{opacity:0}100%{opacity:0.35}}@keyframes fade{0%{opacity:0}100%{opacity:0.35}}@media (max-width: 768px){.set-two.show-result{width:90%}}@media (max-width: 576px){.set-two.show-result{grid-template-columns:1fr 1fr;grid-template-areas:"user computer" "result result";width:100%;grid-row-gap:2rem;row-gap:2rem}.set-two.show-result .bloc-user{grid-area:user}.set-two.show-result .bloc-computer{grid-area:computer}.set-two .bloc{display:grid;order:1}.set-two .bloc h2{order:2;margin-bottom:0;margin-top:1.5rem}.set-two .bloc .choice{order:1}.set-two .result{order:2;grid-area:result}.set-two .container-btn{position:relative;z-index:5}.set-two .btn-choice{width:140px;height:140px;border-width:18px}.set-two .btn-choice .shadow-circle{width:140px;height:140px}.set-two .btn-choice .icon{width:104px;height:97px}.set-two .btn-choice .icon::before{transform:translate(-50%, -50%) scale(0.8)}.set-two .spot{width:140px;height:140px}.set-two .spot:before{width:100px;height:100px}.set-two .win{width:140px;height:140px}.set-two .win .circle-win{width:140px;height:140px}.set-two .win .circle-win span:first-child{width:220px;height:220px}.set-two .win .circle-win span:nth-child(2){width:320px;height:320px}.set-two .win .circle-win span:last-child{width:400px;height:400px}}

footer{text-align:right;padding:1rem 2rem}footer .btn-rules{background:none;outline:none;border:1px solid #ffffff;color:#ffffff;padding:0.5rem 2rem;text-transform:uppercase;letter-spacing:2px;border-radius:0.5rem;cursor:pointer}footer .btn-rules:hover{background-color:#ffffff;color:#3b4363}footer .modal{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);text-align:left;background-color:#ffffff;color:#3b4363;padding:1rem 2rem 1.5rem;border-radius:0.5rem}footer .modal h2{font-size:2rem;margin:0 0 3rem}footer .modal .container-title{position:relative}footer .modal .container-title .cross{border:none;background:none;position:absolute;right:0;top:50%;transform:translateY(-50%);display:inline-block;height:60%;width:20px;cursor:pointer}footer .modal .container-title .cross:before,footer .modal .container-title .cross:after{content:'';display:inline-block;height:20px;width:3px;background:#CCCDD2;position:absolute;top:0;left:8px}footer .modal .container-title .cross::before{transform:rotate(-45deg)}footer .modal .container-title .cross::after{transform:rotate(45deg)}@media (max-width: 768px){footer{text-align:center}}

*{box-sizing:border-box}body{margin:0;background:#1f3756;background:radial-gradient(circle at top, #1f3756 0%, #141539 100%);background-repeat:no-repeat;min-height:100vh;color:#ffffff;font-family:'Barlow Semi Condensed', sans-serif;font-size:14px}main{height:calc(100vh - (197.6px + 1.75rem))}.container{width:700px;margin:0 auto}p{margin:0}.App{padding-top:1.75rem}@media (max-width: 992px){.container{width:90%}}

